export default {
  /**
   * [isInt 是否为正整数]
   * @param  {[int]}  num
   * @return {Boolean}
   */
  isInt(num){
    return /^[1-9]+[0-9]*$/.test(num);
  },

  /**
   * [isPhone 是否为手机号]
   * @param  {[string]}  num
   * @return {Boolean}
   */
  isPhone(num){
    var reg = /(^(([0\+]\d{2,3}-)?(0\d{2,3})-)(\d{7,8})(-(\d{3,}))?$)|(^0{0,1}1[3|4|5|6|7|8|9][0-9]{9}$)/;
    return reg.test(num);
  }
}



