<template>
  <div class="top-bar">
    <span @click="backHistory" class="btn-back" v-if="showback"><img src="../assets/icon_top_back.png" alt="" /></span>
    <h4><img v-if="titleicon" :src="titleicon" style="height:16px;margin-right:3px;position:relative;top:1px;" alt="">{{title}}</h4>
  </div>
</template>
<script>
  export default {
    props:[
      {
        name:'title',
        type:String
      },
      {
        name:'showback',
        type: Boolean,
        default: true
      },
      {
        name:'titleicon',
        type:String
      }
    ],
    methods:{
      backHistory(){
        console.log(this.$route.path)
        switch(this.$route.path) {
          case '/history':
            this.$router.go('/me')
            break;
          case '/devices':
            this.$router.go('/me')
            break;
          case '/bind':
            this.$router.go('/me')
            break;
          case '/binded':
            this.$router.go('/me')
            break;
          default:
            this.$router.go('/')
            break;
        }
      }
    }
  }
</script>
<style lang="scss">
  .top-bar{
    height: 44px;
    line-height: 44px;
    position: relative;
    .btn-back{
      width:30px;
      height: 30px;
      display: inline-block;
      text-align: center;
      margin-top: 5px;
      img{
        height: 21px;
      }
    }
    h4{
      position: absolute;
      z-index: -1;
      top:0;
      font-size: 18px;
      color: rgb(80, 80, 80);
      font-weight: normal;
      text-align: center;
      width: 100%;
    }
  }

</style>



