<template>
  <div style="height:100%;">
    <loading :show="isLoading" position="absolute"></loading>
    <view-box v-ref:view-box>
      <router-view></router-view>
      <v-footer></v-footer>
    </view-box>
  </div>
</template>
<script>
import store from './vuex/store'
import { Tabbar, TabbarItem, Loading, ViewBox} from 'vux/src/components'
import vFooter from './components/v-footer'
export default {
  components: {
    Tabbar,
    TabbarItem,
    Loading,
    ViewBox,
    vFooter
  },
  store: store,
  vuex: {
    getters: {
      route: (state) => state.route,
      isLoading: (state) => state.isLoading,
      user: (state) => state.user
    }
  },
  methods: {
    scrollTop () {
      this.$refs.viewBox.$els.viewBoxBody.scrollTop = 0
    }
  },
  compiled(){
    this.getCurrentUser()
  },
  methods:{
    /**
     * [getCurrentUser 获取当前用户的信息]
     * @return {[type]} [description]
     */
    getCurrentUser() {
      this.$http.get('wx/getUser?openId=' + window.OPENID).then((res)=>{
        if(res.body.success){
          store.dispatch('UPDATE_USER', res.body.data.user);
        }else{
          console.log('获取失败');
        }
      })
    }
  }
}
</script>
<style lang="scss">
body {
  background-color: rgb(239,239,239);
  font-size: 62.5%;
}
html, body {
  height: 100%;
  width: 100%;
  overflow-x: hidden;
}
/*
 * buttons
 */
.btn{
  border-radius: 3px;
  border: none;
  height: 40px;
  font-size: 14px;
  &.btn-default{
    background-color: rgb(234, 191, 32);
    color:#000;
    text-align: center;
  }
  &.btn-block{
    width:100%;
  }
}

.btn-fixed{
  position: absolute;
  bottom: 80px;
  width: 100%;
  left: 0;
  padding: 10px;
}

/*
 * spacing
 */
.mt15{
  margin-top: 15px;
}
.mt20{
  margin-top: 20px;
}
*{
  box-sizing: border-box;
}


/*position*/
.clearfix{
  overflow: auto;
  zoom:1;
}
.fl{
  float:left;
}
.fr{
  float: right;
}

</style>



