<template>
  <div id="tophead">
    <section class="ranking-header">
      <span @click="loadRanking" class="f12">更新</span>
      <h4>排行榜</h4>
      <div class="dropdown pull-right f12">
        <span class="with_arrow" :class="{'active':showMenu}" @click="toggleDrop">{{province}}</span>
        <ul class="dropdown_menu" v-if="showMenu" transition="fade">
          <li @click="changeProvince('全国')">全国</li>
          <li v-if="user.province" @click="changeProvince(user.province)">{{user.province}}</li>
        </ul>
      </div>
    </section>
  </div>
  <section class="ranking-list myself" id="myRank">
    <div class="row">
      <div class="col col-3 ranking-avatar" style="margin-left:15px;text-align:center">
        <img :src="user.headimgUrl" class="circle-avatar" v-if="user.headimgUrl" />
        <img src="../assets/default_avatar.png" class="circle-avatar" v-else/>
      </div>
      <div class="col col-3 ranking-name f16">
        {{user.nickName?user.nickName:'--'}}
        <div class="f14" v-if="myRank.ranking!=0">
         第{{myRank.ranking?myRank.ranking:'-'}}名
        </div>
        <div class="f14" v-else>
         无排名
        </div>
      </div>
      <div class="col col-3 ranking-step" style="width:43.67361%">
        {{myRank.kneelCount?myRank.kneelCount:'0'}}
      </div>
    </div>
  </section>
  <section id="rankingList">
    <scroller lock-x scrollbar-y :height="scrollerH + 'px'" :prevent-default="false" v-ref:scroller>
      <section class="ranking-list">
        <div class="row" v-for='people in rankinglist'>
          <div class="col ranking-num f22">
            {{people.ranking}}
          </div>
          <div class="col ranking-avatar">
            <img :src="people.headimgUrl" class="circle-avatar" v-if="people.headimgUrl" />
            <img src="../assets/default_avatar.png" class="circle-avatar" v-else/>
          </div>
          <div class="col ranking-name f16">
            {{people.nickName}}
          </div>
          <div class="col ranking-step">
            {{people.kneelCount}} <span>次</span>
          </div>
        </div>
      </section>
    </scroller>
  </section>

  </div>
</template>

<script>
import {Scroller,ViewBox} from 'vux/src/components'
export default {
  components: {
    Scroller,
    ViewBox
  },
  vuex: {
    getters: {
      user: (state) => state.user
    }
  },
  data() {
    return {
      rankinglist: [],
      myRank:{},
      province:'全国',
      favnum: 0,
      loaded: false,
      showMenu:false,
      scrollerH:200
    }
  },
  ready() {
    this.loadRanking();
    this.scrollerH = this.scrollH()
  },
  methods: {
    scrollH(){
      let viewH = document.body.clientHeight;
      let headH = document.getElementById("tophead").clientHeight;
      let myRankH = document.getElementById("myRank").clientHeight;
      let scrollerH = viewH - headH - myRankH - 55 - 15;
      return scrollerH;
    },
    /**
     * [toggleDrop 显示下拉]
     * @param  {[type]} e [description]
     * @return {[type]}   [description]
     */
    toggleDrop(e){
      if(!this.showMenu){
        this.showMenu = true;
      }else{
        this.showMenu = false;
      }
    },
    /**
     * [changeProvince 选择省份]
     * @param  {[type]} prov [description]
     * @return {[type]}      [description]
     */
    changeProvince(prov){
      this.province = prov;
      this.showMenu = false;
      this.loadRanking();
    },
    /**
     * [loadRanking 获取我的排名和排名列表]
     * @return {[type]} [description]
     */
    loadRanking(){
      this.getRankingList();
      this.getMyRank();
    },
    /**
     * [getRankingList 获取排名列表]
     * @return {[type]} [description]
     */
    getRankingList(){
      this.$http.get('wx/kneel/ranking?province='+this.province).then((res)=>{
        if(res.body.success){
          this.rankinglist = res.body.data.ranking;
        }
      })
    },
    /**
     * [getMyRank 获取我的排名]
     * @return {[type]} [description]
     */
    getMyRank(){
      this.$http.post('wx/my',{openId:window.OPENID,province:this.province}).then((res)=>{
        if(res.body.success){
          this.myRank = res.body.data.result;
        }
      })
    }
  }
}
</script>

