<style lang="scss">

.bind-form {
    padding: 20px;
    text-align: center;
    &.binded{
      .form-control{
        border:none;
        border-radius:0;
      }
    }
    .input-group {
        position: relative;
        display: table;
        width: 100%;
        border-collapse: separate;
        .input-icon {
            position:absolute;
            left:10px;
            top:9px;
            z-index:2
        }
        .input-error{
          color:red;
          position:absolute;
          bottom:-17px;
        }
    }
    .input-icon-group{
        .form-control{
            text-indent:20px;
        }
    }
    .input-group[class*=col-] {
        float: none;
        padding-right: 0;
        padding-left: 0
    }
    .input-group .form-control {
        position: relative;
        z-index: 2;
        float: left;
        width: 100%;
        margin-bottom: 0
    }
    .input-group-lg>.form-control,
    .input-group-lg>.input-group-addon,
    .input-group-lg>.input-group-btn>.btn {
        height: 46px;
        padding: 10px 16px;
        font-size: 18px;
        line-height: 1.33;
        border-radius: 6px
    }
    .input-group-btn,
    .input-group .form-control {
        display: table-cell
    }
    .btn-checkcode{
        margin-left:10px;
        width:128px;
        max-width:128px;
    }
    .input-group-addon:not(:first-child):not(:last-child),
    .input-group-btn:not(:first-child):not(:last-child),
    .input-group .form-control:not(:first-child):not(:last-child) {
        border-radius: 0
    }
    .input-group-btn {
        width: 1%;
        white-space: nowrap;
        vertical-align: middle
    }
    .form-control {
        display: block;
        width: 100%;
        height: 42px;
        padding: 6px 12px;
        font-size: 14px;
        line-height: 1.42857143;
        color: #555;
        background-color: #fff;
        background-image: none;
        border: 1px solid #ccc;
        border-radius: 4px;
        outline: none;
    }
}

</style>

<template>

<div>
    <topbar title="账号绑定"></topbar>

    <form class="bind-form mt20" method="post">
        <div class="input-group input-icon-group">
            <input type="text" class="form-control" placeholder="手机号" v-model="phone">
            <span class="input-icon"><img src="../assets/icon_phone@2x.png" width="14"></span>
        </div>
        <div class="input-group mt15">
            <input type="text" class="form-control" v-model="checkcode" placeholder="验证码">
            <span class="input-group-btn">
                <button class="btn btn-checkcode" :class="{'btn-primary':start,'btn-default':!start}" type="button" @click="getVerifyCode" :disabled="start">获取验证码<span v-if="start">(<countdown slot="value" :time="second" :start="start" @on-finish="start=false"></countdown>)</span></button>
            </span>
        </div>
        <div class="input-group mt15">
            <button type="button" class="btn btn-primary btn-lg btn-block" :class="{'btn-default':isPassed,'btn-primary':!isPassed}" :disabled="isPassed" @click="setBindPhone">完成</button>
        </div>
    </form>

    <toast :show.sync="show_toast" type="text" :time="2000">{{toast_txt}}</toast>
</div>

</template>

<script>
import store from '../vuex/store';
import Topbar from './Topbar';
import { xInput, Group, Countdown, Toast } from 'vux/src/components';
import Reg from '../common/reg';
export default {
  components: {
    Topbar,
    xInput,
    Group,
    Countdown,
    Toast
  },
  store: store,
  vuex: {
    getters: {
      user: (state) => state.user
    }
  },
  data() {
    return {
        phone:'',
        checkcode:'',
        second:20,
        start:false,
        show_toast:false,
        toast_txt:''
    }
  },
  computed:{
    isPassed(){
      return this.phone!=''&&this.checkcode!=''
    }
  },
  methods:{
    showToast(txt,cb){
      this.toast_txt = txt;
      this.show_toast = true;
    },
    /**
     * [getVerifyCode 获取验证码]
     * @param  {[type]} e
     * @return {[type]}
     */
    getVerifyCode(e){
      if(this.phone==''){
        this.showToast('手机号码不能为空');
      }else{
        if(Reg.isPhone(this.phone)){
          this.$http.get('wx/getVerifyCode?phone=' + this.phone).then((res)=>{
            if(!res.body.success){
              this.showToast(res.body.msg);
            }else{
              this.start=true;
            }
          })
        }else{
          this.showToast('手机号码格式不正确');
        }
      }
    },
    /**
     * [setBindPhone 绑定手机号]
     */
    setBindPhone(){

      if(this.phone==''){
        this.showToast('手机号码不能为空');
      }else{
        if(Reg.isPhone(this.phone)){
          this.$http.post('wx/bindPhone',{openId: window.OPENID, phone: this.phone, verifyCode: this.checkcode}).then((res)=>{
            if(!res.body.success){
              this.showToast(res.body.msg);
            }else{
              this.showToast('绑定成功');
              setTimeout(function(){
                this.$router.go('/me');
              }.bind(this),1000);
            }
          })
        }else{
          this.showToast('手机号码格式不正确');
        }
      }
    }
  }
}

</script>



