<style lang="scss">

.device-list {
    padding: 10px;
    .device-item {
        position: relative;
        background: #fff;
        border-radius: 5px;
        padding: 15px;
        margin-bottom:10px;
        box-shadow: 0 2px 3px rgba(0, 0, 0, .1);
        img {
            width: 50px;
            vertical-align: top;
        }
        .device-info {
            display: inline-block;
            margin-left: 10px;
            p {
                color: grey;
                a {
                    color: #346df0
                }
            }
        }
        .device-checked{
          position: absolute;
          right:10px;
          top:30px;
        }
    }
    .device-add {
        box-shadow: none;
        border: 1px solid #dcdcdc;
        background: none;
        padding:20px;
        .add-icon {
            transform: rotate(45deg);
            width: 24px;
        }
        .device-info{
          text-align: center;
          width: 100%;
          text-align: center;
          left: 0;
          font-size: 16px;
          position: absolute;
          color:grey;
        }
    }
    .device-item__nobg{
      background: none;
      border: none;
      box-shadow: none;
      border-bottom: 1px solid #c9c9c9;
      border-radius: 0;
    }
    .device-remove{

    }
}



.devices-addbox {
    height: 100%;
    position: fixed;
    background-color: rgb(239, 239, 239);
    width: 100%;
    z-index: 101;
    bottom:0;
}

.slide-transition {
  display: inline-block; /* 否则 scale 动画不起作用 */
}
.slide-enter {
  animation: slide-in .4s;
}
.slide-leave {
  animation: slide-out .4s;
}
@keyframes slide-in {
  0% {
    transform: translate(0,100%);
  }
  100% {
    transform: translate(0,0);
  }
}
@keyframes slide-out {
  0% {
    transform: translate(0,0);
  }
  100% {
    transform: translate(0,100%);
  }
}

</style>

<template>

<div>
    <topbar title="设备管理"></topbar>

    <div class="device-list">
        <div class="device-item" v-for="device in devices" v-if="devices.length!=0" >
            <div class="device-item__inner" >
                <img src="../assets/icon_futon@2x.png" v-if="device.deviceType=='智能蒲团'" alt="" />
                <img src="../assets/icon_glove@2x.png" v-if="device.deviceType=='智能手套'" alt="" />
                <img src="../assets/icon_mud@2x.png" v-if="device.deviceType=='智能磕垫'" alt="" />
                <div class="device-info">
                    <h2>{{device.deviceType}}</h2>
                    <p>mac: {{device.mac}}</p>
                </div>
                <input type="checkbox" class="device-checked" name="name" value={{device.deviceId}} v-model="checkedDevices">
            </div>
        </div>
        <div class="device-remove btn-fixed" v-if="devices.length!=0" >
            <x-button type="primary" :disabled="stopdel" @click="removeDevice">{{deltxt}}</x-button>
        </div>
        <div class="device-item device-add" v-if="devices.length==0" @click="this.showAddBox = true">
            <div class="device-item__inner">
                <img src="../assets/icon_close@2x.png" class="add-icon" alt="" />
                <div class="device-info">
                    添加设备
                </div>
            </div>
        </div>
    </div>
    <div class="devices-addbox" id="addBox" v-if="showAddBox" transition="slide">
        <div class="top-bar">
            <span class="btn-back"><img src="../assets/icon_close@2x.png" style="margin-left:10px;height:18px;" alt=""  @click="this.showAddBox = false" /></span>
            <h4>绑定提示</h4>
        </div>
        <div class="device-list">
            <div class="device-item device-item__nobg" style="border-bottom:0">
                <h3 style="font-size:14px;text-align:center;margin-bottom:15px;">请用微信扫一扫设备上如下样式的二维码</h3>
                <img src="../assets/scan_tips.png" style="width:100%">
            </div>

            <div class="device-remove btn-fixed">
                <x-button type="primary" @click="this.showAddBox = false">先不添加设备</x-button>
            </div>
        </div>
    </div>
</div>

</template>

<script>

import Topbar from './Topbar';
import xButton from 'vux/src/components/x-button';
import _ from 'lodash';

export default {
    props: ['selected'],
    components: {
        Topbar,xButton
    },
    data() {
        return {
            devices: [],
            showadd:false,
            showAddBox:false,
            deltxt:'移除设备',
            checkedDevices:[],
            stopdel:false
        }
    },
    ready(){
      this.getBindedDevices()
    },
    methods:{
      /**
       * [getBindedDevices 获取绑定的设备]
       * @return {[type]} [description]
       */
      getBindedDevices(){
        this.$http.get('wx/device?openId='+window.OPENID).then((res)=>{
          let result = res.body;
          if(result.success){
            this.devices = result.data.devices;
          }else{
            console.log(result.msg);
          }
          this.devices.length==0?this.showadd=true:this.showadd=false
        })
      },
      /**
       * [removeDevice 移除设备]
       * @return {[type]} [description]
       */
      removeDevice(){
        let self = this;
        let buff_devices = this.devices;
        let checkedDevices = this.checkedDevices;
        self.stopdel = true;
        self.deltxt = '移除中..';
        if(checkedDevices.length>0){
          checkedDevices.forEach(function(device_id){

            self.$http.post('wx/device/unBind',{openId:window.OPENID, deviceId: device_id}).then((res)=>{
              if(res.body.success){
                /* 移除选中设备 */
                let obj = _.find(self.devices, function(o) {
                  return o.deviceId == device_id;
                });
                self.devices.$remove(obj)
              }else{
                alert('删除失败，请稍后重新尝试');
              }
            })
          })
        }else{
          alert('请先选中设备')
        }
        self.stopdel = false;
        self.deltxt = '移除设备';
      }
    }
}

</script>



