import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

const state = {
  isLoading: false,
  user: Object,
  kneels:[]
}
export default new Vuex.Store({
  state,
  mutations: {
    UPDATE_LOADING(state, status) {
      state.isLoading = status
    },
    UPDATE_USER(state, user) {
      state.user = user
    },
    UPDATE_KNEELS(state, kneels) {
      state.kneels = kneels
    }
  }
})



