<template>

<div>
    <topbar title="录入历史"></topbar>
    <div class="" v-show="hasHistory" style="display:none">
      <p style="text-align:center;font-size:16px;margin-top:30px;color:#aaa">您已录入过历史数据，不用重复录入了</p>
    </div>
    <form class="bind-form mt20" method="post" v-show="noHistory" style="display:none">
        <div class="input-group">
            <input type="text" class="form-control" placeholder="输入历史数据" v-model="history" v-on:keyup="historyError=''">
            <div class="input-error" v-show="history==''||historyError!=''">{{historyError}}</div>
        </div>
        <div class="input-group mt15">
            <button type="button" class="btn btn-primary btn-lg btn-block" :class="{'btn-default':isPassed,'btn-primary':!isPassed}" :disabled="!isPassed" @click="setHistory">完成</button>
        </div>
    </form>
</div>

</template>

<script>
import Topbar from './Topbar';
import Reg from '../common/reg';
import { xInput, Group, Countdown } from 'vux/src/components';
export default {
  components: {
    Topbar,
    xInput,
    Group,
  },
  data() {
    return {
        history:'',
        historyError: '',
        hasHistory:false,
        noHistory:false,
    }
  },
  created(){
    this.checkHistory();
  },
  computed:{
    isPassed(){
      return this.history!=''
    }
  },
  methods:{
    /**
     * [setHistory 录入历史]
     */
    setHistory(){
      if(Reg.isInt(this.history)){
        this.historyError = '';
        this.$http.post('wx/sethistory',{openId:window.OPENID, kneelCount: this.history}).then((res)=>{
          if(res.body.success){
            window.history.back();
          }else{
            this.historyError = '提交失败,请稍后重试';
          }
        })
      }else{
        this.historyError = '数据格式不正确';
      }
    },
    /**
     * [checkHistory 检测历史是否已录入]
     * @return {[type]} [description]
     */
    checkHistory(){
      this.$http.get('wx/checkhistory?openId='+window.OPENID).then((res)=>{
        this.hasHistory = res.body.success?false:true
        this.noHistory = !this.hasHistory
      })
    }
  }
}

</script>



