
<template>
<div>
  <div class="vux-pop-out-transition">
      <div class="center">
          <img v-bind:src="user.headimgUrl" class="circle-avatar circle-l">
          <p class="vux-notice">{{user.nickName}}</p>
      </div>
  </div>
  <group>
    <cell title="昵称" :value="user.nickName"></cell>
    <cell title="性别" :value="user.sex==1?'男':'女'"></cell>
    <cell title="年龄" link="javascript:void(0)" :value="user.age" @click="show=true"></cell>
    <cell title="省份" :value="user.province"></cell>
  </group>
  <group>
    <cell title="账号绑定" :link="bindOrNot" ></cell>
    <cell title="设备管理" link="/devices" ></cell>
    <cell title="录入历史" link="/history"></cell>
  </group>

  <dialog :show.sync="show" :scroll="false">
    <div class="dialog-edit">
      <p class="dialog-title">修改年龄</p>
      <div class="img-box" style="padding:20px 40px;">
        <input type="text" class="txt-bline" name="" v-model="newage" placeholder={{user.age}}>
        <div class="error" v-if="!checkAge(newage)">{{ageError}}</div>
        <button type="button" class="btn btn-default btn-lg btn-block mt15" @click="updateUser">完成</button>
      </div>
      <span class="vux-close" @click="show=false"></span>
    </div>
  </dialog>
</div>
</template>

<script>
import store from '../vuex/store';
import {Address, Cell,Group,AddressChinaData, Dialog} from 'vux/src/components';
import Reg from '../common/reg';
export default {
    components: {
      Address, Cell,Group,AddressChinaData,Dialog
    },
    store:store,
    vuex: {
      getters: {
        user: (state) => state.user
      }
    },
    data() {
        return {
           addressData: AddressChinaData,
           show: false,
           newage: this.user.age,
           ageError:'',
           hasHistory:true
        }
    },
    computed:{
      bindOrNot(){
        return this.user.phone==''?'/bind':'/binded';
      }
    },
    methods:{
      /**
       * [getCurrentUser 获取用户信息]
       * @return {[type]} [description]
       */
      getCurrentUser() {
        this.$http.get('wx/getUser?openId=' + window.OPENID).then((res)=>{
          if(res.body.success){
            store.dispatch('UPDATE_USER', res.body.data.user);
          }else{
            console.log('获取失败');
          }
        })
      },
      /**
       * [updateUser 更新用户信息]
       * @return {[type]} [description]
       */
      updateUser(){
        if(Reg.isInt(this.newage)){
          this.ageError = '';
          this.$http.post('wx/updateUser',{openId:window.OPENID, age: this.newage}).then((res)=>{
            if(res.body.success){
              this.show = false;
              this.user.age = this.newage;
            }else{
              this.ageError = '提交失败,请稍后重试';
            }
          })
        }else{
          this.ageError = '年龄格式不正确';
        }
      },
      checkAge(age){
        return Reg.isInt(age)
      }

    }
}

</script>

<style lang="less" scoped>
@import '~vux/src/styles/close';
.center {
  margin-top: 30px;
  text-align: center;
}
.txt-bline{
  font-size: 30px;
  text-align: center;
  width: 100%;
  background: none;
  border:0;
  border-bottom:1px solid #dedede;

  outline: none;

}
.vux-notice {
  color: #666;
  font-size: 20px;
}
.dialog-edit{
  padding:10px 0;
}
.vux-title {
  vertical-align: middle;
  text-align: center;
  color: #04BE02;
  display: inline-block;
  width: 75px;
  height: 75px;
  line-height: 75px;
  border-radius: 50%;
}

</style>



